<template><section><h1>radio 单选框</h1>
<hr>
<p>单选框通常用于一组选项中，只能单独选择一项的情况。</p>
<h2>何时使用</h2>
<p>用于在多个备选项中选中单个状态。</p>
<p>和 Select 的区别是，Radio 所有选项默认可见，方便用户在比较中选择，因此选项不宜过多。</p>
<h2>基本单选框</h2>
<p>最简单的用法。
需要设置v-model绑定变量，选中意味着变量的值为相应 Radio label属性的值，label可以是String或者Number。</p>
<demo-box>
                    <div slot="demo"><html><head></head><body><cat-radio v-model="radio" label="1">单选1</cat-radio>
<cat-radio v-model="radio" label="2">单选2</cat-radio>


<p>你喜欢吃啥</p>
<cat-radio v-model="radio2" label="1">黄焖鸡</cat-radio>
<cat-radio v-model="radio2" label="2">沙县小吃</cat-radio>
</body></html></div>
                    <div slot="source-code"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">cat-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"1"</span>&gt;</span>单选1<span class="hljs-tag">&lt;/<span class="hljs-name">cat-radio</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"2"</span>&gt;</span>单选2<span class="hljs-tag">&lt;/<span class="hljs-name">cat-radio</span>&gt;</span>


<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>你喜欢吃啥<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio2"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"1"</span>&gt;</span>黄焖鸡<span class="hljs-tag">&lt;/<span class="hljs-name">cat-radio</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio2"</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"2"</span>&gt;</span>沙县小吃<span class="hljs-tag">&lt;/<span class="hljs-name">cat-radio</span>&gt;</span>
</code></pre>
</div></demo-box><h2>禁用状态</h2>
<p>不可用状态</p>
<demo-box>
                    <div slot="demo"><html><head></head><body><cat-radio v-model="radio3" :label="1" :disabled="disabled3">禁用</cat-radio>
<cat-radio v-model="radio3" :label="2" :disabled="disabled3">禁用</cat-radio>
<cat-button size="small" type="danger" @click="disabled3 = !disabled3">切换禁用</cat-button>
</body></html></div>
                    <div slot="source-code"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">cat-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio3"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"1"</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"disabled3"</span>&gt;</span>禁用<span class="hljs-tag">&lt;/<span class="hljs-name">cat-radio</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-radio</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radio3"</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"2"</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"disabled3"</span>&gt;</span>禁用<span class="hljs-tag">&lt;/<span class="hljs-name">cat-radio</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-button</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"disabled3 = !disabled3"</span>&gt;</span>切换禁用<span class="hljs-tag">&lt;/<span class="hljs-name">cat-button</span>&gt;</span>
</code></pre>
</div></demo-box><h2>单选组合</h2>
<p>适用于在多个互斥的选项中选择的场景</p>
<p>@change为单选组合值发生改变的时触发的回调函数, 返回值data为Radio label属性的值</p>
<demo-box>
                    <div slot="demo"><html><head></head><body><cat-radio-group :options="radioGruops1" v-model="radioGruop1" :disabled="disabledGroup1" @change="radioGroup1"></cat-radio-group>
<p>当前选择: {{ radioGruop1 }}</p>
<cat-button size="small" type="danger" @click="disabledGroup1 = !disabledGroup1">切换禁用</cat-button>

</body></html></div>
                    <div slot="source-code"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">cat-radio-group</span> <span class="hljs-attr">:options</span>=<span class="hljs-string">"radioGruops1"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radioGruop1"</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"disabledGroup1"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"radioGroup1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-radio-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>当前选择: {{ radioGruop1 }}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-button</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"disabledGroup1 = !disabledGroup1"</span>&gt;</span>切换禁用<span class="hljs-tag">&lt;/<span class="hljs-name">cat-button</span>&gt;</span>

</code></pre>
<pre v-pre=""><code v-pre="" class="language-js">&lt;script&gt;
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">disabledGroup</span>: <span class="hljs-literal">false</span>,
      <span class="hljs-attr">radioGruop1</span>: <span class="hljs-string">''</span>,
      <span class="hljs-attr">radioGruops1</span>: [
        { <span class="hljs-attr">label</span>: <span class="hljs-string">'Apple'</span>, <span class="hljs-attr">value</span>: <span class="hljs-string">'Apple'</span> },
        { <span class="hljs-attr">label</span>: <span class="hljs-string">'Pear'</span>, <span class="hljs-attr">value</span>: <span class="hljs-string">'Pear'</span> },
        { <span class="hljs-attr">label</span>: <span class="hljs-string">'Orange'</span>, <span class="hljs-attr">value</span>: <span class="hljs-string">'Orange'</span> }
      ]
    }
  },
  <span class="hljs-attr">methods</span>: {
    radioGroup1 (data) {
      <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'单选按钮组的回调函数, 当前值为'</span> + data)
    }
  }
}
&lt;<span class="hljs-regexp">/script&gt;
</span></code></pre>
</div></demo-box><h2>单选按钮组合</h2>
<p>按钮样式的单选组合。</p>
<p>@change为单选组合值发生改变的时触发的回调函数, 返回值data为Radio label属性的值</p>
<p>size 为按钮大小  small normal large 默认为normal</p>
<p>disabled 和 size 都以选项为主, 整体为辅， 如果子项里面不存在， 才会去选择整体的。</p>
<demo-box>
                    <div slot="demo"><html><head></head><body><cat-radio-group :options="radioGruops2" v-model="radioGruop2" :disabled="disabledGroup2" @change="radioGroup2" type="button" size="samll"></cat-radio-group>
<cat-radio-group :options="radioGruops2" v-model="radioGruop2" :disabled="disabledGroup2" type="button" size="normal"></cat-radio-group>
<cat-radio-group :options="radioGruops2" v-model="radioGruop2" :disabled="disabledGroup2" type="button" size="large"></cat-radio-group>
<p>当前选择: {{ radioGruop2 }}</p>
<cat-button size="small" type="danger" @click="disabledGroup2 = !disabledGroup2">切换禁用</cat-button>
</body></html></div>
                    <div slot="source-code"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">cat-radio-group</span> <span class="hljs-attr">:options</span>=<span class="hljs-string">"radioGruops2"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radioGruop2"</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"disabledGroup2"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"radioGroup2"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"samll"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-radio-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-radio-group</span> <span class="hljs-attr">:options</span>=<span class="hljs-string">"radioGruops2"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radioGruop2"</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"disabledGroup2"</span>  <span class="hljs-attr">type</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"normal"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-radio-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-radio-group</span> <span class="hljs-attr">:options</span>=<span class="hljs-string">"radioGruops2"</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"radioGruop2"</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"disabledGroup2"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"large"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-radio-group</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>当前选择: {{ radioGruop2 }}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-button</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"danger"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"disabledGroup2 = !disabledGroup2"</span>&gt;</span>切换禁用<span class="hljs-tag">&lt;/<span class="hljs-name">cat-button</span>&gt;</span>
</code></pre>
<pre v-pre=""><code v-pre="" class="language-js">&lt;script&gt;
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">disabledGroup2</span>: <span class="hljs-literal">false</span>,
      <span class="hljs-attr">radioGruop2</span>: <span class="hljs-string">'Apple'</span>,
      <span class="hljs-attr">radioGruops2</span>: [
        { <span class="hljs-attr">label</span>: <span class="hljs-string">'上海'</span>, <span class="hljs-attr">text</span>: <span class="hljs-string">'上海'</span> },
        { <span class="hljs-attr">label</span>: <span class="hljs-string">'北京'</span>, <span class="hljs-attr">text</span>: <span class="hljs-string">'北京'</span> },
        { <span class="hljs-attr">label</span>: <span class="hljs-string">'深圳'</span>, <span class="hljs-attr">text</span>: <span class="hljs-string">'深圳'</span> }
      ]
    }
  },
  <span class="hljs-attr">methods</span>: {
    radioGroup2 (data) {
      <span class="hljs-built_in">console</span>.log(<span class="hljs-string">'单选按钮组的回调函数, 当前值为'</span> + data)
    }
  }
}
&lt;<span class="hljs-regexp">/script&gt;

</span></code></pre>
</div></demo-box><h2>Radio 参数</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td>Radio的value</td>
<td><code v-pre="">string</code> , <code v-pre="">number</code> , <code v-pre="">boolean</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>name</td>
<td>原生属性</td>
<td><code v-pre="">string</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>禁止点击</td>
<td>Boolean</td>
<td><code v-pre="">true</code>, <code v-pre="">false</code></td>
<td>false</td>
</tr>
</tbody>
</table>
<h2>RadioButton 参数</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>label</td>
<td>Radio的value</td>
<td><code v-pre="">string</code> , <code v-pre="">number</code> , <code v-pre="">boolean</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>name</td>
<td>原生属性</td>
<td><code v-pre="">string</code></td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>禁止点击</td>
<td>Boolean</td>
<td><code v-pre="">true</code>, <code v-pre="">false</code></td>
<td>false</td>
</tr>
<tr>
<td>size</td>
<td>按钮大小</td>
<td>string</td>
<td><code v-pre="">small</code>, <code v-pre="">normal</code> , <code v-pre="">large</code></td>
<td>normal</td>
</tr>
</tbody>
</table>
<h2>RadioGroup 参数</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>options</td>
<td>radio数据 子项：label- 对应value, text - 对应显示文字， size, disabled</td>
<td>Array</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>禁止点击</td>
<td>Boolean</td>
<td><code v-pre="">true</code>, <code v-pre="">false</code></td>
<td>false</td>
</tr>
<tr>
<td>size</td>
<td>按钮大小， 仅是单选按钮时生效</td>
<td>string</td>
<td><code v-pre="">small</code>, <code v-pre="">normal</code> , <code v-pre="">large</code></td>
<td>normal</td>
</tr>
<tr>
<td>type</td>
<td>单选类型</td>
<td>string</td>
<td>``, <code v-pre="">button</code></td>
<td>-</td>
</tr>
<tr>
<td>@change</td>
<td>回调函数</td>
<td>function</td>
<td>-</td>
<td>返回当前选择的radio的value</td>
</tr>
</tbody>
</table>


</section></template>
<style lang="scss" scoped>
  p {
    padding: 10px 0;
    line-height: 35px;
  }

  .cat-radio-group {
    display: block;
    margin-bottom : 16px;
  }
</style>
<script>
export default {
  data () {
    return {
      radio: '',
      radio2: '',
      radio3: 1,
      radio4: '',
      disabled3: true,
      disabledGroup1: false,
      radioGruop1: '',
      radioGruops1: [
        { label: 'Apple', text: 'Apple' },
        { label: 'Pear', text: 'Pear' },
        { label: 'Orange', text: 'Orange' }
      ],
      disabledGroup2: false,
      radioGruop2: 'Apple',
      radioGruops2: [
      { label: '上海', text: '上海' },
      { label: '北京', text: '北京' },
      { label: '深圳', text: '深圳' }
      ]
    }
  },
  methods: {
    radioGroup1 (data) {
      console.log('单选组的回调函数, 当前值为' + data)
    },
    radioGroup2 (data) {
      console.log('单选按钮组的回调函数, 当前值为' + data)
    }
  }
}
</script>


