<template><section><h1>inputNumber 数字输入框</h1>
<hr>
<p>通过鼠标或键盘，输入范围内的数值。</p>
<h2>何时使用</h2>
<p>当需要获取标准数值时。</p>
<h3>基础数字输入框</h3>
<p>注: 仅允许输入标准的数字值，可定义范围和每次改变量</p>
<demo-box>
                    <div slot="demo"><html><head></head><body><p>默认输入框</p>
<cat-input-number v-model="value1"></cat-input-number>
<p>{{value1}}</p>

<p>最小值为5, 最大值为10</p>
<cat-input-number v-model="value2" :max="10" :min="5"></cat-input-number>
<p>{{value2}}</p>

<p>step = 5</p>
<cat-input-number v-model="value5" :step="5"></cat-input-number>
<p>{{value5}}</p>
</body></html></div>
                    <div slot="source-code"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>默认输入框<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-input-Number</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input-number</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{value1}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>最小值为5, 最大值为10<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-input-Number</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value2"</span> <span class="hljs-attr">:max</span>=<span class="hljs-string">"10"</span> <span class="hljs-attr">:min</span>=<span class="hljs-string">"5"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input-number</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{value2}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>step = 5<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-input-Number</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value5"</span> <span class="hljs-attr">:step</span>=<span class="hljs-string">"5"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input-number</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{value5}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
</code></pre>
</div></demo-box><h3>小数输入框</h3>
<p>注: 改变step的值， 来实现小数</p>
<demo-box>
                    <div slot="demo"><html><head></head><body><p>step = 0.5</p>
<cat-input-number v-model="value3" :step="0.5"></cat-input-number>
<p>{{value3}}</p>

<p>step = 0.8, max = 8, min = 0</p>
<cat-input-number v-model="value4" :step="0.8" :max="8" :min="0"></cat-input-number>
<p>{{value4}}</p>

</body></html></div>
                    <div slot="source-code"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>step = 0.5<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-input-Number</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value3"</span> <span class="hljs-attr">:step</span>=<span class="hljs-string">"0.5"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input-number</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{value3}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>step = 0.8, max = 8, min = 0<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-input-Number</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value4"</span> <span class="hljs-attr">:step</span>=<span class="hljs-string">"0.8"</span> <span class="hljs-attr">:max</span>=<span class="hljs-string">"8"</span> <span class="hljs-attr">:min</span>=<span class="hljs-string">"0"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input-number</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>{{value4}}<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>

</code></pre>
</div></demo-box><h3>三种大小</h3>
<p>注: 高度分别为 large  36px、 normal 32px 和 small 28px, 默认为normal</p>
<demo-box>
                    <div slot="demo"><html><head></head><body><cat-input-number v-model="value6" size="small"></cat-input-number>
<cat-input-number v-model="value6"></cat-input-number>
<cat-input-number v-model="value6" size="large"></cat-input-number>

</body></html></div>
                    <div slot="source-code"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">cat-input-Number</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value6"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"small"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input-number</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-input-Number</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value6"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input-number</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-input-Number</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value6"</span> <span class="hljs-attr">size</span>=<span class="hljs-string">"large"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input-number</span>&gt;</span>

</code></pre>
</div></demo-box><h3>不可用状态</h3>
<p>注: 设置属性 disabled 禁用输入框</p>
<demo-box>
                    <div slot="demo"><html><head></head><body><cat-input-number v-model="value7" :disabled="dis"></cat-input-number>
<cat-button @click="dis = !dis" type="primary">改变disabled</cat-button>
</body></html></div>
                    <div slot="source-code"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">cat-input-Number</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"value7"</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"dis"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">cat-input-number</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">cat-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"dis = !dis"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>改变disabled<span class="hljs-tag">&lt;/<span class="hljs-name">cat-button</span>&gt;</span>
</code></pre>
</div></demo-box><h2>InputNumber 参数</h2>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>value</td>
<td>输入框的值，可通过 <code v-pre="">v-model</code> 绑定</td>
<td>Number</td>
<td>-</td>
<td>-</td>
</tr>
<tr>
<td>size</td>
<td>输入框尺寸</td>
<td>String</td>
<td><code v-pre="">small</code>, <code v-pre="">normal</code>, <code v-pre="">large</code></td>
<td>normal</td>
</tr>
<tr>
<td>step</td>
<td>每次递增或递减的数目</td>
<td>Number</td>
<td>-</td>
<td>1</td>
</tr>
<tr>
<td>min</td>
<td>最小值</td>
<td>Number</td>
<td>-</td>
<td>-Infinity</td>
</tr>
<tr>
<td>max</td>
<td>最大值</td>
<td>Number</td>
<td>-</td>
<td>Infinity</td>
</tr>
<tr>
<td>disabled</td>
<td>是否禁用输入框</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>readonly</td>
<td>是否设置成只读</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
<tr>
<td>autofocus</td>
<td>是否自动聚焦到输入框</td>
<td>Boolean</td>
<td>-</td>
<td>false</td>
</tr>
</tbody>
</table>
<h2>InputNumber 事件</h2>
<p>注: 通过@focus 传入事件即可调用</p>
<table class="table"><thead>
<tr>
<th>事件名称</th>
<th>说明</th>
<th>返回参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>focus</td>
<td>获得焦点时触发</td>
<td>event</td>
</tr>
<tr>
<td>blur</td>
<td>失去焦点时触发</td>
<td>event</td>
</tr>
<tr>
<td>change</td>
<td>在 Input 值改变时触发</td>
<td>event</td>
</tr>
<tr>
<td>enter</td>
<td>在 Input 按下enter触发</td>
<td>event</td>
</tr>
</tbody>
</table>


</section></template>
<style lang="scss" scoped>
  .cat-input-number {
    margin: 10px 10px 0 0;
    width: 200px!important;
  }

  p {
    line-height: 35px;
  }
</style>
<script>
export default {
  data () {
    return {
      value1: 0,
      value2: 0,
      value3: 0.5,
      value4: 1.6,
      value5: 5,
      value6: 0,
      value7: 10,
      dis: true
    }
  }
}
</script>


